@import '../../global.scss';

.intro {
    background-color: white;
    display: flex;

    @include mobile{
        flex-direction: column;
        align-items: center;
    }

    .left {
        flex: 0.5;
        overflow: hidden;

        .imgContainer {
            width: 700px;
            height: 700px;
            background-color: $mainColor;
            border-radius: 50%;
            display: flex;
            align-items: flex-start;
            justify-content: center;
            float: left;

            @include mobile {
                align-items: flex-start;
            }

            img {
                height: 20%;
                
                @include mobile {
                    height: 50%;
                }
            }
        }
    }

    .right {
        flex: 0.5;
        position: relative;

        .wrapper {
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            @include mobile {
                padding-left: 0;
                align-items: center;
            }

            h1 {
                font-size: 60px;
                margin: 10px 0;

                @include mobile {
                    font-size: 40px;
                    text-align: center;
                }
            }

            h2 {
                font-size: 35px;
            }

            h3 {
                font-size: 30px;
                
                @include mobile {
                    font-size: 20px;
                }

                span {
                    font-size: inherit;
                    color: crimson;
                }

                .ityped-cursor {
                    animation: blink 1s infinite;
                }

                @keyframes blink {
                    50% {
                        opacity: 1;
                    }
                    100% {
                        opacity: 0;
                    }
                }
            }
        }
        a {
            position: absolute;
            bottom: 10px;
            left: 92%;
            color: black;

            @include mobile {
                left: 45%;    
            }

            .icon {
                font-size: 50px;
                animation: arrowBlink 1s infinite;      
            }

            @keyframes arrowBlink {
                to {
                    opacity: 0;
                }
            }
        }
    }
}